.carousel-matrix .swiper-slide {
  transition: opacity;
  transition-duration: 1000ms;
  transition-timing-function: cubic-bezier(0.33, 1, 0.68, 1);
}

.carousel-matrix .swiper-slide {
  opacity: 0.4;
}

.carousel-matrix .swiper-slide .captions {
  @apply hidden;
}

.carousel-matrix .swiper-slide-active {
  opacity: 1;
}

.carousel-matrix .swiper-slide-active .captions {
  @apply block;
}

.carousel-matrix .swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: -24px;
}

.carousel-matrix .swiper-button-prev::after,
.carousel-matrix .swiper-button-next::after {
  content: '';
}

.carousel-matrix .swiper-button-prev {
  left: 35px;
}

.carousel-matrix .swiper-button-next {
  right: 35px;
}

@media screen and (max-width: 1024px) {
  .swiper-button-prev,
  .swiper-button-next {
    display: none;
  }
}
