/* Plus minus qty */
.skin-2 .num-in {
  background: #ffffff;
  border-radius: 3px;
  height: 42px;
  width: 110px;
  border: 1px solid #c4c4c4;
  float: left;
  position: absolute;
  right: 0;
  top: 2em;
}

.skin-2 .num-in span {
  width: 40%;
  display: block;
  height: 40px;
  float: left;
  position: relative;
}

.skin-2 .num-in span:before,
.skin-2 .num-in span:after {
  content: '';
  position: absolute;
  background-color: #667780;
  height: 2px;
  width: 10px;
  top: 50%;
  left: 50%;
  margin-top: -1px;
  margin-left: -5px;
}

.skin-2 .num-in span.plus:after {
  transform: rotate(90deg);
}

.skin-2 .num-in input {
  float: left;
  width: 20%;
  height: 40px;
  border: none;
  text-align: center;
}

