  top: 0;
}

.price-box .values div,
.price-box small div {
  display: inline-block;
  vertical-align: top;
}
.price-box .values {
  margin: 0;
  font-weight: 500;
  color: var(--primary);
}
.price-box .values > div:first-child {
  margin-right: 2px;
}
.price-box .values > div:last-child {
  margin-left: 2px;
}
.price-box small {
  color: var(--text);
  display: block;
  margin-top: 8px;
  font-size: 14px;
}
.price-box .slider {
  margin-top: 10px;
}

.price-box .slider {
  --primary: #706f73;
  --handle: #fff;
  --handle-active: #becfff;
  --handle-hover: #e9efff;
  --handle-border: 2px solid var(--primary);
  --line: #cdd9ed;
  --line-active: var(--primary);
  height: 23px;
  width: 100%;
  position: relative;
  pointer-events: none;
}
.price-box .slider .ui-slider-handle {
  --y: 0;
  --background: var(--handle);
  cursor: -webkit-grab;
  cursor: grab;
  -webkit-tap-highlight-color: transparent;
  top: 0;
  width: 23px;
  height: 23px;
  transform: translateX(-50%);
  position: absolute;
  outline: none;
  display: block;
  pointer-events: auto;
}
.price-box .slider .ui-slider-handle div {
  width: 23px;
  height: 23px;
  border-radius: 50%;
  transition: background 0.4s ease;
  transform: translateY(calc(var(--y) * 1px));
  border: var(--handle-border);
  background: var(--background);
}

.price-box .slider .ui-slider-handle:hover {
  --background: var(--handle-hover);
}

.price-box .slider .ui-slider-handle:active {
  --background: var(--handle-active);
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.price-box .slider svg {
  --stroke: var(--line);
  display: block;
  height: 83px;
}
.price-box .slider svg path {
  fill: none;
  stroke: var(--stroke);
  stroke-width: 1;
}
.price-box .slider .active,
.price-box .slider > svg {
  position: absolute;
  top: -30px;
  height: 83px;
}
.price-box .slider > svg {
  left: 0;
  width: 100%;
}
.price-box .slider .active {
  position: absolute;
  overflow: hidden;
  left: calc(var(--l) * 1px);
  right: calc(var(--r) * 1px);
}
.price-box .slider .active svg {
  --stroke: var(--line-active);
  position: relative;
  left: calc(var(--l) * -1px);
  right: calc(var(--r) * -1px);
}
.price-box .slider .active svg path {
  stroke-width: 2;
}

